@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body, html {
    margin: 0px;
    padding: 0px;

    width: 100%;
    height: 100%;
}
html {
  background-image: url('https://wallpaperaccess.com/full/1192976.jpg');
  background-position:center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
#root {
  width: 100%;
  height: 100%;
}


/* animation - loading list */
.isFetching {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin: 10vh auto;
  border: 2px dashed red;

  -webkit-animation-name: seach-fetching;

          animation-name: seach-fetching;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes seach-fetching {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(360deg) scale(1.3);
              transform: rotate(360deg) scale(1.3);
  }
}

@keyframes seach-fetching {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(360deg) scale(1.3);
              transform: rotate(360deg) scale(1.3);
  }
}

a {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}

.btn {
  width: 20px;
  height: 20px;
}

.App {
  width: 100%;
  height: 100%;
  font-family: 'Source Sans Pro', sans-serif;

}





footer {    
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  
    width: 100%;
    align-content: center;
  
    font-size: 1.2rem;
    
    background-image: url('https://www.grunge.com/img/gallery/bizarre-things-weve-sent-to-outer-space/intro-1617974432.jpg');
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .footer-wrapper {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  
    margin: 30px;
  
    color: white;
  }
.Navbar {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    align-content: center;

    background-image: url('https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/download-1583327893.jpeg');
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;

    padding: 10px;
}

.Navbar-logo, .Navbar-nav  {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;

    margin-right: 20px;
}

.Navbar-nav li {
    margin-right: 20px;
    text-decoration: none;
}
.Navbar-nav li:last-child {
    margin-right: 0px;
}
.selected {
    color: blue !important;
}

@media (max-width: 600px) {
    .Navbar {
        flex-direction: column;
    }
    .Navbar-nav {
        margin: 20px;
        margin-right: 0px;
    }
}
.ListPhoto-wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;

    width: 100%;
    height: 100%;

    padding: 15px;
}

.ListPhoto {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.photos_item {
    cursor: pointer;
    width: 300px;
    margin-bottom: 10px;
}

.load {
    margin: 15px 0;
}
.load Button:last-child {
    margin-left: 10px;
}

.photos_item-info {
    margin-top: -44px;
    background: rgba(255, 255, 255, 0.7);
    position: relative;
}

@media (max-width: 600px) {
    .ListPhoto-wrapper {
        justify-content: center;
    }
}

.ModalPhoto {
    height: 90%;

    position: fixed;
    
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0px 0px 10px 10px rgba(34, 60, 80, 0.16);
}

.ModalPhoto img {
    height: 100%;
}

.ModalPhoto-bnt {
    margin: 10px;
    cursor: pointer;
    position: absolute;
}

@media (max-width: 1000px) {
    .ModalPhoto {
        width: 90%;
        height: unset;
    }
    .ModalPhoto img {
        width: 100%;
        height: unset;
    }
}
.AboutRover {
    padding: 15px;
    color: black;

    font-size: 1.2rem;
}
.Main {
    display: flex;
    flex-direction: row;
    min-height: 100%;
}

.left-bar{
    /* height: 100%; */
    background: rgba(247, 133, 72, 0.466);
    width: 320px;
}

.form-wrapper {
    padding: 30px 0px;
}

.form-filters {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    width: 300px;
    /* height: 100% */
}
.form-filters label {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: flex-start;

    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 1.3rem;
    margin-bottom: 10px;
}
.form-filters select, input {
    width: 250px;
    background: transparent;
    outline: unset;
    border: none;
    padding: 5px 5px;
    font-size: 1.2rem;

    border-bottom: 1px solid #000;
}
.form-filters input {
    width: 242px;
}

.setSearch {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.setSearch-text {
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;

    background: rgba(0, 0, 0, 0.35);
    padding: 10px;
    border-radius: 10px;
}


@media (max-width: 640px) {
    .Main {
        flex-direction: column;
    }
    .left-bar {
        width: 100%;
    }
    .form-filters {
        width: 100%;
    }
}

.welcome {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    flex-wrap: wrap;

    text-align: center;
