}

.welcome-text {
    max-width: 900px;
    font-size: 1.5rem;
    color: white;

    background: rgba(44, 44, 44, 0.6);
    padding: 15px;
    border-radius: 10px;
}
.Rovers-link {
    font-size: 1.5rem;
    color: white;
    text-decoration: underline;
}
